<template>
	<div id="store-fee">
		<public-header title="佣金设置"></public-header>
		<div class="content">
			<van-radio-group v-model="account.fee_takeout.type">
				<van-cell-group>
					<van-cell title="外卖单佣金计算方式">
						<div slot="right-icon" class="c-gray" @click="onTogglePopup('fee_takeout')">
							具体设置<van-icon name="arrow"></van-icon>
						</div>
					</van-cell>
					<van-cell title="订单百分比抽成" :label="account.fee_takeout.type == 1 ? `抽成比例为${account.fee_takeout.fee_rate}%,最低抽成金额${account.fee_takeout.fee_min}元` : ''" clickable @click="account.fee_takeout.type = 1" class="border-0px no-fix">
						<van-radio :name="1" />
					</van-cell>
					<van-cell title="固定抽成" :label="account.fee_takeout.type == 2 ? `固定抽成${account.fee_takeout.fee}元` : ''" clickable @click="account.fee_takeout.type = 2" class="border-0px">
						<van-radio :name="2" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<van-radio-group v-model="account.fee_selfDelivery.type" class="margin-10-t">
				<van-cell-group>
					<van-cell title="自提单佣金计算方式">
						<div slot="right-icon" class="c-gray" @click="onTogglePopup('fee_selfDelivery')">
							具体设置<van-icon name="arrow"></van-icon>
						</div>
					</van-cell>
					<van-cell title="订单百分比抽成" :label="account.fee_selfDelivery.type == 1 ? `抽成比例为${account.fee_selfDelivery.fee_rate}%,最低抽成金额${account.fee_selfDelivery.fee_min}元` : ''" clickable @click="account.fee_selfDelivery.type = 1" class="border-0px no-fix">
						<van-radio :name="1" />
					</van-cell>
					<van-cell title="固定抽成" :label="account.fee_selfDelivery.type == 2 ? `固定抽成${account.fee_selfDelivery.fee}元` : ''" clickable @click="account.fee_selfDelivery.type = 2" class="border-0px">
						<van-radio :name="2" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<van-radio-group v-model="account.fee_instore.type" class="margin-10-t">
				<van-cell-group>
					<van-cell title="店内单佣金计算方式">
						<div slot="right-icon" class="c-gray" @click="onTogglePopup('fee_instore')">
							具体设置<van-icon name="arrow"></van-icon>
						</div>
					</van-cell>
					<van-cell title="订单百分比抽成" :label="account.fee_instore.type == 1 ? `抽成比例为${account.fee_instore.fee_rate}%,最低抽成金额${account.fee_instore.fee_min}元` : ''" clickable @click="account.fee_instore.type = 1" class="border-0px">
						<van-radio :name="1" />
					</van-cell>
					<van-cell title="固定抽成" :label="account.fee_instore.type == 2 ? `固定抽成${account.fee_instore.fee}元` : ''" clickable @click="account.fee_instore.type = 2" class="border-0px">
						<van-radio :name="2" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<van-radio-group v-model="account.fee_goods.type" class="margin-10-t" v-if="0">
				<van-cell-group>
					<van-cell title="特殊商品佣金计算方式">
						<div slot="right-icon" class="c-gray" @click="onTogglePopup()">
							具体设置<van-icon name="arrow"></van-icon>
						</div>
					</van-cell>
					<van-cell title="订单百分比抽成" label="抽成比例为5%" clickable @click="feeGoodsType = '1'" class="border-0px">
						<van-radio name="1" />
					</van-cell>
					<van-cell title="固定抽成" label="固定抽成5元" clickable @click="feeGoodsType = '2'" class="border-0px">
						<van-radio name="2" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<van-radio-group v-model="account.fee_paybill.type" class="margin-10-t">
				<van-cell-group>
					<van-cell title="买单佣金计算方式">
						<div slot="right-icon" class="c-gray" @click="onTogglePopup('fee_paybill')">
							具体设置<van-icon name="arrow"></van-icon>
						</div>
					</van-cell>
					<van-cell title="订单百分比抽成" :label="account.fee_paybill.type == 1 ? `抽成比例为${account.fee_paybill.fee_rate}%,最低抽成金额${account.fee_paybill.fee_min}元` : ''" clickable @click="account.fee_paybill.type = 1" class="border-0px">
						<van-radio :name="1" />
					</van-cell>
					<van-cell title="固定抽成" :label="account.fee_paybill.type == 2 ? `固定抽成${account.fee_paybill.fee}元` : ''" clickable @click="account.fee_paybill.type = 2" class="border-0px">
						<van-radio :name="2" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title">最低提现金额</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input v-model.number="account.fee_limit"><span>元</span>
						</div>
					</div>
				</van-cell>
				<van-cell>
					<div slot="title">提现费率</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input v-model.number="account.fee_rate"><span>%</span>
						</div>
					</div>
				</van-cell>
				<van-cell>
					<div slot="title">提现费用</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input v-model.number="account.fee_min"><span>元</span>
						</div>
						<span class="c-gray">~</span>
						<div class="input-unit">
							<input v-model.number="account.fee_max"><span>元</span>
						</div>
					</div>
				</van-cell>
				<van-cell>
					<div slot="title">提现周期</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input v-model.number="account.fee_period"><span>天</span>
						</div>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div slot="title">允许商家使用超级优惠券</div>
					<iswitch slot="right-icon" :value="store.data.superCoupon.status" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="store.data.superCoupon.status"></iswitch>
				</van-cell>
				<van-cell>
					<div slot="title">每月最多发放</div>
					<div slot="right-icon">
						<div class="input-unit">
							<input v-model.number="store.data.superCoupon.max_limit"><span>张</span>
						</div>
					</div>
				</van-cell>
			</van-cell-group>
			<div class="padding-15">
				<van-button size="normal" type="primary" :disabled="!islegal" block @click="onSubmit" class="font-16">保存修改</van-button>
			</div>
		</div>
		<!--外卖弹出层-->
		<van-popup v-model="popupStatus.fee_takeout" position="bottom">
			<div class="popup-title">
				<span class="c-gray" @click="onTogglePopup('fee_takeout')">取消</span>
				<span v-if="account.fee_takeout.type == 1">外卖单佣金计算方式(按比例)</span>
				<span v-else-if="account.fee_takeout.type == 2">外卖单佣金计算方式(固定)</span>
				<span class="c-primary"  @click="onTogglePopup('fee_takeout')">确定</span>
			</div>
			<div class="popup-content">
				<template v-if="account.fee_takeout.type == 1">
					<van-checkbox-group v-model="account.fee_takeout.items_yes">
						<van-cell-group>
							<van-cell title="抽成项目" />
							<van-cell
									v-for="(item,index) in feeYesGroup"
									clickable
									:key="index"
									:title="item.title"
									@click="onToggleFeeYes(index, 'fee_takeout')"
									class="border-0px"
									v-if="item.type != 'serve_fee'"
							>
								<van-checkbox :name="item.type" ref="feeYes-fee_takeout" />
							</van-cell>
						</van-cell-group>
					</van-checkbox-group>
					<van-checkbox-group v-model="account.fee_takeout.items_no" class="margin-10-t">
						<van-cell-group>
							<van-cell title="不抽成项目" />
							<van-cell
									clickable
									title="商户活动补贴"
									@click="onToggleFeeNo('fee_takeout')"
									class="border-0px"
							>
								<van-checkbox name="store_discount_fee" ref="feeNo-fee_takeout" />
							</van-cell>
						</van-cell-group>
					</van-checkbox-group>
					<van-cell-group class="margin-10-t">
						<van-cell>
							<div slot="title">抽成比例</div>
							<div slot="right-icon">
								<div class="input-unit">
									<input v-model.number="account.fee_takeout.fee_rate"><span>%</span>
								</div>
							</div>
						</van-cell>
						<van-cell>
							<div slot="title">最低抽成金额</div>
							<div slot="right-icon">
								<div class="input-unit">
									<input v-model.number="account.fee_takeout.fee_min"><span>元</span>
								</div>
							</div>
						</van-cell>
					</van-cell-group>
				</template>
				<template v-else-if="account.fee_takeout.type == 2">
					<van-cell-group>
						<van-cell>
							<div slot="title">固定抽成</div>
						</van-cell>
						<van-cell>
							<div slot="title">抽成金额</div>
							<div slot="right-icon">
								<div class="input-unit">
									<input v-model.number="account.fee_takeout.fee"><span>元</span>
								</div>
							</div>
						</van-cell>
					</van-cell-group>
				</template>
			</div>
		</van-popup>
		<!--自提单弹出层-->
		<van-popup v-model="popupStatus.fee_selfDelivery" position="bottom">
			<div class="popup-title">
				<span class="c-gray" @click="onTogglePopup('fee_selfDelivery')">取消</span>
				<span v-if="account.fee_selfDelivery.type == 1">自提单佣金计算方式(按比例)</span>
				<span v-else-if="account.fee_selfDelivery.type == 2">自提单佣金计算方式(固定)</span>
				<span class="c-primary"  @click="onTogglePopup('fee_selfDelivery')">确定</span>
			</div>
			<div class="popup-content">
				<template v-if="account.fee_selfDelivery.type == 1">
					<van-checkbox-group v-model="account.fee_selfDelivery.items_yes">
						<van-cell-group>
							<van-cell title="抽成项目" />
							<van-cell
									v-for="(item,index) in feeYesGroup"
									clickable
									:key="index"
									:title="item.title"
									@click="onToggleFeeYes(index, 'fee_selfDelivery')"
									class="border-0px"
									v-if="item.type != 'serve_fee' && item.type != 'delivery_fee'"
							>
								<van-checkbox :name="item.type" ref="feeYes-fee_selfDelivery" />
							</van-cell>
						</van-cell-group>
					</van-checkbox-group>
					<van-checkbox-group v-model="account.fee_selfDelivery.items_no" class="margin-10-t">
						<van-cell-group>
							<van-cell title="不抽成项目" />
							<van-cell
									clickable
									title="商户活动补贴"
									@click="onToggleFeeNo('fee_selfDelivery')"
									class="border-0px"
							>
								<van-checkbox name="store_discount_fee" ref="feeNo-fee_selfDelivery" />
							</van-cell>
						</van-cell-group>
					</van-checkbox-group>
					<van-cell-group class="margin-10-t">
						<van-cell>
							<div slot="title">抽成比例</div>
							<div slot="right-icon">
								<div class="input-unit">
									<input v-model.number="account.fee_selfDelivery.fee_rate"><span>%</span>
								</div>
							</div>
						</van-cell>
						<van-cell>
							<div slot="title">最低抽成金额</div>
							<div slot="right-icon">
								<div class="input-unit">
									<input v-model.number="account.fee_selfDelivery.fee_min"><span>元</span>
								</div>
							</div>
						</van-cell>
					</van-cell-group>
				</template>
				<template v-else-if="account.fee_selfDelivery.type == 2">
					<van-cell-group>
						<van-cell>
							<div slot="title">固定抽成</div>
						</van-cell>
						<van-cell>
							<div slot="title">抽成金额</div>
							<div slot="right-icon">
								<div class="input-unit">
									<input v-model.number="account.fee_selfDelivery.fee"><span>元</span>
								</div>
							</div>
						</van-cell>
					</van-cell-group>
				</template>
			</div>
		</van-popup>
		<!--店内单弹出层-->
		<van-popup v-model="popupStatus.fee_instore" position="bottom">
			<div class="popup-title">
				<span class="c-gray" @click="onTogglePopup('fee_instore')">取消</span>
				<span v-if="account.fee_instore.type == 1">店内单佣金计算方式(按比例)</span>
				<span v-else-if="account.fee_instore.type == 2">店内单佣金计算方式(固定)</span>
				<span class="c-primary"  @click="onTogglePopup('fee_instore')">确定</span>
			</div>
			<div class="popup-content">
				<template v-if="account.fee_instore.type == 1">
					<van-checkbox-group v-model="account.fee_instore.items_yes">
						<van-cell-group>
							<van-cell title="抽成项目" />
							<van-cell
								v-for="(item,index) in feeYesGroup"
								clickable
								:key="index"
								:title="item.title"
								@click="onToggleFeeYes(index, 'fee_instore')"
								class="border-0px"
								v-show="item.type == 'serve_fee' || item.type == 'price'"
							>
								<van-checkbox :name="item.type" ref="feeYes-fee_instore" />
							</van-cell>
						</van-cell-group>
					</van-checkbox-group>
					<van-checkbox-group v-model="account.fee_instore.items_no" class="margin-10-t">
						<van-cell-group>
							<van-cell title="不抽成项目" />
							<van-cell
									clickable
									title="商户活动补贴"
									@click="onToggleFeeNo('fee_instore')"
									class="border-0px"
							>
								<van-checkbox name="store_discount_fee" ref="feeNo-fee_instore" />
							</van-cell>
						</van-cell-group>
					</van-checkbox-group>
					<van-cell-group class="margin-10-t">
						<van-cell>
							<div slot="title">抽成比例</div>
							<div slot="right-icon">
								<div class="input-unit">
									<input v-model.number="account.fee_instore.fee_rate"><span>%</span>
								</div>
							</div>
						</van-cell>
						<van-cell>
							<div slot="title">最低抽成金额</div>
							<div slot="right-icon">
								<div class="input-unit">
									<input v-model.number="account.fee_instore.fee_min"><span>元</span>
								</div>
							</div>
						</van-cell>
					</van-cell-group>
				</template>
				<template v-else-if="account.fee_instore.type == 2">
					<van-cell-group>
						<van-cell>
							<div slot="title">固定抽成</div>
						</van-cell>
						<van-cell>
							<div slot="title">抽成金额</div>
							<div slot="right-icon">
								<div class="input-unit">
									<input v-model.number="account.fee_instore.fee"><span>元</span>
								</div>
							</div>
						</van-cell>
					</van-cell-group>
				</template>
			</div>
		</van-popup>
		<!--买单弹出层-->
		<van-popup v-model="popupStatus.fee_paybill" position="bottom">
			<div class="popup-title">
				<span class="c-gray" @click="onTogglePopup('fee_paybill')">取消</span>
				<span v-if="account.fee_paybill.type == 1">买单佣金计算方式(按比例)</span>
				<span v-else-if="account.fee_paybill.type == 2">买单佣金计算方式(固定)</span>
				<span class="c-primary"  @click="onTogglePopup('fee_paybill')">确定</span>
			</div>
			<div class="popup-content">
				<template v-if="account.fee_paybill.type == 1">
					<van-cell-group class="margin-10-t">
						<van-cell>
							<div slot="title">抽成比例</div>
							<div slot="right-icon">
								<div class="input-unit">
									<input v-model.number="account.fee_paybill.fee_rate"><span>%</span>
								</div>
							</div>
						</van-cell>
						<van-cell>
							<div slot="title">最低抽成金额</div>
							<div slot="right-icon">
								<div class="input-unit">
									<input v-model.number="account.fee_paybill.fee_min"><span>元</span>
								</div>
							</div>
						</van-cell>
					</van-cell-group>
				</template>
				<template v-else-if="account.fee_paybill.type == 2">
					<van-cell-group>
						<van-cell>
							<div slot="title">固定抽成</div>
						</van-cell>
						<van-cell>
							<div slot="title">抽成金额</div>
							<div slot="right-icon">
								<div class="input-unit">
									<input v-model.number="account.fee_paybill.fee"><span>元</span>
								</div>
							</div>
						</van-cell>
					</van-cell-group>
				</template>
			</div>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				feeYesGroup: [
					{
						type: 'price',
						title: '商品费用'
					},
					{
						type: 'box_price',
						title: '餐盒费'
					},
					{
						type: 'pack_fee',
						title: '包装费'
					},
					{
						type: 'delivery_fee',
						title: '配送费(仅限店内模式)'
					},
					{
						type: 'serve_fee',
						title: '店内服务费'
					}
				],
				feeNoGroup:[
					{
						type: 'store_discount_fee',
						title: '商户活动补贴'
					}
				],

				popupStatus: {
					fee_takeout: false,
					fee_selfDelivery: false,
					fee_instore: false,
					fee_goods: false, //?????
					fee_paybill: false,
				},
				id: 0,
				account: {
					fee_takeout: {
						type: 1,
						fee: 0,
						fee_min: 0,
						fee_rate: 0,
						items_no: [],
						items_yes: [],
					},
					fee_selfDelivery: {
						type: 1,
						fee: 0,
						fee_min: 0,
						fee_rate: 0,
						items_no: [],
						items_yes: [],
					},
					fee_instore: {
						type: 1,
						fee: 0,
						fee_min: 0,
						fee_rate: 0,
						items_no: [],
						items_yes: [],
					},
					fee_goods: [
						{
							type: 1,
							fee_rate: 0,
							fee: 0,
						}
					],
					fee_paybill: {
						type: 1,
						fee: 0,
						fee_min: 0,
						fee_rate: 0,
					},
					fee_limit: 0,
					fee_rate: 0,
					fee_min: 0,
					fee_max: 0,
					fee_period: 0,
				},
				store: {
					data: {
						superCoupon: {
							status: 0,
							max_limit: 0,
						}
					}
				},
				islegal: false,
				showPreLoading: true
			}
		},
		components: {
			publicHeader,
			iswitch
		},
		methods: {
			onToggleFeeYes(index, type) {
				let key = 'feeYes-' + type;
				this.$refs[key][index].toggle();
			},
			onToggleFeeNo(type) {
				let key = 'feeNo-' + type;
				this.$refs[key].toggle();
			},
			onTogglePopup(type) {
				this.popupStatus[type] = !this.popupStatus[type];
			},
			onLoad() {
				var that = this;
				getData({
					url: 'plateform/store/shop/fee',
					vue: that,
					data: {
						id: that.id
					},
					success: (res) =>{
						that.account = that.util.extend(that.account, res.account);
						that.store = that.util.extend(that.store, res.store);
						that.islegal = true;
					}
				});
			},
			onSubmit() {
				var that = this;
				if(!that.islegal) {
					return false;
				}
				that.islegal = false;
				setData({
					url: 'plateform/store/shop/fee',
					vue: that,
					data: {
						id: that.id,
						account: JSON.stringify(that.account),
						store: JSON.stringify(that.store)
					},
					redirect: that.util.getUrl({path: 'pages/merchant/store/index', query: {id: that.id}}),
					fail: function(res) {
						that.util.$toast(res);
						that.islegal = true;
						return;
					}
				})
			},
			jsToggleSwitch(res) {
				this.util.jsToggleSwitch({
					vue: this,
					key: res.key || res.keys,
					value: res.value
				});
			}
		},
		created() {
			if(this.$route.query && this.$route.query.id > 0) {
				this.id = parseInt(this.$route.query.id)
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#store-fee .van-icon-arrow{
		color: #999;
		font-size: 12px;
		line-height: 24px;
		margin-left: 5px;
	}
	#store-fee .van-cell__label{
		line-height: 1;
		margin-top: -3px;
	}
	#store-fee .iswitch{
		min-width: 72px;
		width: 72px;
		height: 22px;
		font-size: 12px;
	}
	#store-fee .van-popup.popup-set{
		width:100%;
	}
</style>


